import 'bootstrap';
import '@popperjs/core';
import $ from 'jquery';
import select2 from "select2"
select2();
import { getAccessToken, issueJwt, renewToken, tokenExpired } from './token';
import { setMessage, joinChatRoom, populateChatRoom, setChatScrollBottom, resetChatRoom } from './chat';
window.$ = $;

$(function () {
    $('#room-users-field').select2({width: '100%'});

    let storedAccessToken = getAccessToken();

    if (!storedAccessToken || storedAccessToken === null) {
        issueJwt()
    }

    $('#send-message').on('click', setMessage);

    $('#message').on('keydown', function(e) {
        if ((e.which === 13 || e.key == 'Enter') && !e.shiftKey) {
            e.stopPropagation();
            setMessage();
            return false;
        }
    });

    let currentRoom = null;

    $('.chat').on('click', function() {
        const roomId = $(this).data('id');

        if (currentRoom === roomId) return;

        showRoom(roomId);
        currentRoom = roomId;
    });

    function showRoom(roomId) {
        function callRoomFromAJAX() {
            return $.ajax({
                url: '/chat/show/' + roomId,
                method: 'GET',
                dataType: 'json',
                headers: {
                    "Authorization": "Bearer " + getAccessToken()
                },
                success: function(response) {
                    if (response.error) {
                        alert(response.error);
                    } else {
                        populateChatRoom(response);
                        joinChatRoom(response.room.id);
                        setChatScrollBottom();
                    }
                },
                error: function(xhr, status, error) {
                    if (xhr.status === 401) { // 401 Unauthorized
                        location.reload();
                    } else {
                        console.error('AJAX error:', status, error);
                    }
                }
            });
        }
    
        function handleTokenAndCallRoom() {
            if (tokenExpired()) {
                renewToken().then(() => {
                    callRoomFromAJAX();
                }).catch(error => {
                    console.error('Token renewal failed:', error);
                    location.reload();
                });
            } else {
                callRoomFromAJAX();
            }
        }
    
        handleTokenAndCallRoom();
    
        if ($(window).width() <= 768) {
            $('#empty-conversation').addClass('d-none');
            $('.chat-rooms').addClass('d-none');
            $('.chat-messages').removeClass('d-none');
        }
    }

    $('.back-button').on('click', function(){
        $('.chat-rooms').removeClass('d-none');
        $('.chat-messages').addClass('d-none');

        resetChatRoom();
        currentRoom = null;

        const emptyConversation = $('<div class="align-items-center d-flex justify-content-center h-100 text-muted" id="empty-conversation">').text('Please select a chat room to join the conversation!');

        $('#chat-body').append(emptyConversation);
    });

    $(window).on('resize', function() {
        if ($(window).width() >= 768) {
            $('.chat-rooms').removeClass('d-none');
            $('.chat-messages').removeClass('d-none');
        } else {
            if (currentRoom) {
                $('.chat-rooms').addClass('d-none');
                $('.chat-messages').removeClass('d-none');
            } else {
                $('.chat-rooms').removeClass('d-none');
                $('.chat-messages').addClass('d-none');
            }
        }
    }).trigger('resize');
    // End Chat Logic

    // Create Room 
    $('#save-room-button').on('click', function(e) {
        e.preventDefault();
        const formData = $('#create-room-form').serialize();
    
        $.ajax({
            url: '/chat/create',
            method: 'POST',
            data: formData,
            dataType: 'json',
            headers: {
                "Authorization": "Bearer " + getAccessToken()
            },
            success: function(data) {
                console.log(data.test);
            },
            error: function(xhr, status, error) {
                console.error('Error:', error);
            }
        });
    });
});