import {ajax} from 'jquery';

export function issueJwt() {
    let accessToken = null;

    ajax({
        url: "auth/token",
        type: "GET",
        dataType: 'json',
        success: function(data) {
            setAccessToken(data.access_token);
            setRefreshToken(data.refresh_token);
            accessToken = data.access_token;
        },
    });

    return accessToken;
}

export function renewToken() {
    const refreshToken = getRefreshToken();
    
    if (!refreshToken) {
        throw Error('Refresh token not found');
    }

    return ajax({
        url: '/auth/refresh',
        method: 'POST',
        data: {
            refresh_token: refreshToken
        },
        success: function(response) {
            const newAccessToken = response.access_token;
            setAccessToken(newAccessToken);
        }
    });
}

function getRefreshToken() {
    const token = window.localStorage.getItem('refresh_token');

    return token;
}

export function getAccessToken() {
    const token = window.localStorage.getItem('access_token');

    return token;
}

function setRefreshToken(token) {
    const isValid = tokenValidation(token);

    if (!isValid) {
        throw new Error('Invalid JWT token');  
    }

    return window.localStorage.setItem('refresh_token', token);
}

function setAccessToken(token) {
    const isValid = tokenValidation(token);

    if (!isValid) {
        throw new Error('Invalid JWT token');  
    }
    
    window.localStorage.setItem('access_token', token);
}

function tokenValidation(token) {
    if (!token || typeof token !== 'string') {
        return false;
    }

    // Check token parts (header, payload, signature)
    const tokenParts = token.split('.');
    if (tokenParts.length !== 3) {
        return false;
    }

    return true;
}

export function tokenExpired() {
    const token = getAccessToken();
    
    if (!token) throw Error('Missing token'); // Token doesn't exist or expired
    
    const decodedToken = JSON.parse(atob(token.split('.')[1]));
    const currentTime = Math.floor(Date.now() / 1000);

    return decodedToken.exp < currentTime;
}