function formatTime(date) {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', hour12: false });
}

function formatDateTime(date) {
    return date.toLocaleDateString([], { day: 'numeric', month: 'numeric', year: 'numeric', hour: '2-digit', minute: '2-digit', hour12: false });
}

function formatDayAndTime(date) {
    return date.toLocaleDateString([], { weekday: 'short', hour: '2-digit', minute: '2-digit', hour12: false });
}

function isSameDay(date1, date2) {
    return date1.toDateString() === date2.toDateString();
}

function isWithinAWeek(date1, date2) {
    const oneWeekAgo = new Date(date2);
    oneWeekAgo.setDate(date2.getDate() - 7);
    return date1 > oneWeekAgo;
}

export function getFormattedTime(timestamp) {
    let formattedTime;
    const now = new Date();
    const date = new Date(timestamp * 1000);

    if (isNaN(date)) {
        throw new Error("Invalid timestamp");
    }

    if (isSameDay(date, now)) {
        formattedTime = formatTime(date);
    } else if (isWithinAWeek(date, now)) {
        formattedTime = formatDayAndTime(date);
    } else {
        formattedTime = formatDateTime(date);
    }

    return formattedTime;
}